<template>
  <div>
    <van-swipe @change="onChange" v-if="product">
      <van-swipe-item v-for="(item,index) in product.storeInfo.slider_image" :key="index">
        <van-image  class="item-img" :src="item" />
      </van-swipe-item>
      <template #indicator>
        <div class="custom-indicator">{{ current + 1 }}/{{product.storeInfo.slider_image.length}}</div>
      </template>    
    </van-swipe>
    

    <div class="price-title">
     <a class="a-text" href="#">{{storeInfo.store_name}}</a>
     <span class="sp">￥{{storeInfo.price}}</span>
     <p class="vip">￥{{storeInfo.vip_price}}</p>
 
    <van-image class="vip-img"
     :src="require('@/assets/login/vip.png')"/>

     <div class="store-info">
     
     </div>
    
     <div class="bt">
       <p>原价：￥{{storeInfo.vip_price}} </p>
       <p>库存：{{storeInfo.stock}} 件</p>
       <p>销量：{{storeInfo.fsales}} 件</p>
     </div>

     
    

    </div>



<van-cell class="Praise" value="0% 用户好评 "  is-link>
  <!-- 使用 title 插槽来自定义标题 -->
  <template class="user-text" #title>用户评价
  </template>
</van-cell>



<!-- 精品推荐 -->
<div class="good-list">

<h3>精品推荐</h3>

 <van-grid class="van-grid-box" :column-num="3" v-for="item in good_list" :key="item.id">
   
  <van-grid-item>

 
            <van-image width="100%" class="van-img" :src="item.image" />
   

  </van-grid-item>
   <p>{{item.store_name}}</p>
    <span>￥{{item.price}}</span> 
</van-grid>
<div class="null"></div>
</div>


    <!-- 加入购物车 -->


 
<van-goods-action class="cart">
  
  <van-goods-action-icon icon="chat-o" text="客服" @click="noClickIcon" />

  <van-goods-action-icon icon="cart-o" text="购物车" :badge="totoNum" @click="Jupmcart"/>

  <van-goods-action-icon icon="shop-o" text="店铺" @click="noClickIcon"/>
  
  <van-goods-action-button type="warning" text="加入购物车"  @click="addCart"/>
  
  <van-goods-action-button type="danger" text="立即购买"  @click="buy"/>
</van-goods-action>
   
    <van-sku
  v-model="showCart"
  :sku="sku"
  :goods="goods"

  @add-cart="onAddCartClicked"
/>
  </div>
</template>
<script>
export default {
  props: ["id"],
  data() {
    return {
      product: null,
      current: 0,
      showCart: false,
      storeInfo:[],
      good_list:[],
      similarity:[],
    

  
   sku: {
        //初始化demo数据
        // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
        // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
        tree: [
          {
            k: "颜色", //属性名称
            k_id: "1", //属性的ID值 唯一 在当前产品里面唯一
            k_s: "s1", //属性的k_s值 对应list里面组合键名
            v: [
              //颜色属性可用值列表
              {
                id: "30349", //属性ID
                name: "红色", //属性名
                //属性图片，默认只有第一行属性有
                imgUrl: "https://b.yzcdn.cn/vant/sku/shoes-1.png",
              },
              {
                id: "1215",
                name: "白色",
                imgUrl: "https://b.yzcdn.cn/vant/sku/shoes-1.png",
              },
            ],
          },
          {
            k: "尺寸",
            k_id: "2",
            v: [
              {
                id: "1193",
                name: "40码",
              },
              {
                id: "1194",
                name: "42码",
              },
            ],
            k_s: "s2",
            count: 2,
          },
        ],
        // 所有 sku 的组合列表，如下是：白色1、白色2、天蓝色1、天蓝色2
        list: [
          {
            id: 2259,
            price: 120, //价格
            s1: "1215", //对应属性列表里面 k_s 等于s1的属性id值
            s2: "1193",
            stock_num: 20, //库存
            goods_id: 946755, //产品ID
          },
          {
            id: 2260,
            price: 110,
            s1: "1215",
            s2: "1194",
            stock_num: 2,
            goods_id: 946755,
          },
          {
            id: 2257,
            price: 130,
            s1: "30349",
            s2: "1193",
            stock_num: 40,
            goods_id: 946755,
          },
          {
            id: 2258,
            price: 100,
            s1: "30349",
            s2: "1194",
            stock_num: 50,
            goods_id: 946755,
          },
        ],
        price: "0",
        stock_num: 227, // 商品总库存
        none_sku: false, // 是否无规格商品
        hide_stock: false, // 是否隐藏剩余库存
      },
      goods: {
        // 数据结构见下方文档
      },
      messageConfig: {
        // 数据结构见下方文档
      },
    };
  },
  created() {
    this.$axios.get("api/product/detail/" + this.id).then((d) => {
      if (d.data.status) {
        this.product = d.data.data;
        this.good_list=d.data.data.good_list;
        let arrs=d.data.data.productAttr;
        this.storeInfo=d.data.data.storeInfo;
        this.similarity=d.data.data.similarity;
        let listValue=d.data.data.productValue;
      console.log('输出',d)
      //sku 数据转换
      let id=1;
      let idobj={};
      let ssk=1;
      this.sku.list=[];
      //获取产品属性里面第一个值默认图片
      this.goods.picture=listValue[Object.keys(listValue)[1]].image;
      //循环产品属性 组装购物车 属性tree
      this.sku.tree=arrs.map((atr,ads)=>{
          let va=atr.attr_value.map((at,index)=>{
            id++;
            idobj[at.attr]=id;
            let attrs= {
              id: id,
              name: at.attr
            }
            if(ads<1){
              attrs.imgUrl=listValue[Object.keys(listValue)[index]].image
            }
            return attrs;
          })
          var ks='s'+ssk;
          ssk++;
          return {
                k: atr.attr_name,
                k_id: atr.attr_name,
                v: va,
                k_s:ks
          }
      });
       // 循环产品属性组合,组装sku 列表
        if (listValue.length == 0) {
          this.sku.list.push({
            goods_id: this.id,
            stock_num: 100000,
            price: this.product.storeInfo.price * 1000,
            imgUrl: this.product.storeInfo.slider_image[0],
            // goods_id:
          });
          return;
        }
        for (let key in listValue) {
          let v = listValue[key];
          let keys = key.split(",");
          // console.log('11v',v.price)
          this.sku.list.push({
            id: v.unique,
            price: v.price * 100, //价格
            s1: idobj[keys[0]],
            s2: idobj[keys[1]],
            stock_num: v.stock, //库存
            goods_id: v.product_id,
            imgUrl: v.image,
          });
        }
        // console.log(this.sku.list);
        // console.log(this.sku.list.goods_id);
      } else {
        this.$toast("产品加载错误...");
      }
    });
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
      totoNum: function () {
      return this.cartva.length;
    },
    noClickIcon(){

    },
    Jupmcart(){
this.$router.push("/cart");
    },
    buy(){

    },
    addCart(){
      this.showCart=true;
    },
    onAddCartClicked(data){
      this.$toast('添加购物车成功!');
      this.showCart=false;
      this.$axios.post('/api/cart/add',{
        cartNum: data.selectedNum,//添加数量
        new: 0,
        productId: data.selectedSkuComb.goods_id,//产品ID
        uniqueId: data.selectedSkuComb.id,
      }).then(d=>{
        if(d.data.status!==200){
          this.$router.push('/login'); 
        }
        // this.$toast('添加购物车成功!');
        console.log("添加购物车成功",d)
      })
    }
  },
};
</script>
<style lang="less" scoped>
.custom-indicator {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 2px 5px;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.1);
}
.item-img img{
  width: 100%;
  display: block;
  
}
.a-text{
  text-align: center;
  display: block;
  font-size: 16px;
  color: #82848F;
}
.van-grid-box{
  display: inline-block;
  width: 125px;
  height: 175px;
  .van-img{
    width: 100%;
    height: 120px;

  }

}

h3{
  text-align: center;
}
.good-list{
  p{
    height: 20px;
    overflow: hidden;
    text-align: center;
  }
  span{
    display: block;
    text-align: center;
    color: rgb(206, 65, 65);
  }
}
.null{
  height: 60px;
}
.Price-title{
  display: flex;
  height: 200px;
}
.sp{
    font-size: 1.4rem;
    margin-left: 2px;
    color: red;
  }
.vip{
    display: inline-block;
    margin-left: 10px;
    font-size: 0.4rem;

  }
  .vip-img{
    display: inline-block;
    width: 30px;
  }
  .store-info{
    text-align: center;
  }
.bt{
  display: flex;
 justify-content: space-between;
 padding: 0 10px;
 p{
   font-size: 0.8rem;
   color: #82848F;
 }
}
</style>